import React, { useState, useCallback, useEffect } from 'react';
import { Box, Typography } from '@mui/material';
import { grey } from '@mui/material/colors';
import { useSpring, animated, config } from 'react-spring';
import RouteLink from 'components/Utils/RouteLink';

const RecruitMentorCircle = () => {
  const [viewBoxSize, setViewboxSize] = useState(0);
  const ref = useCallback((el: HTMLElement | null) => {
    if (el != null) {
      setViewboxSize(el.clientWidth);
    }
  }, []);
  const circleSize = viewBoxSize / 3;

  const [blink, setBlink] = useState(false);
  const styles = useSpring({
    opacity: blink ? 1 : 0.5,
    transform: `scale(${blink ? 1 : 0.9})`,
    transformOrigin: 'center',
    config: config.wobbly,
  });
  useEffect(() => {
    const interval = setInterval(() => {
      setBlink(state => !state);
    }, 800);
    return () => {
      clearInterval(interval);
    };
  }, []);

  return (
    <Box
      component={RouteLink}
      to="https://lubycon.notion.site/3a06c7b26d894b7d95aefa135eaa88ea"
      sx={{ textDecoration: 'none' }}
    >
      <Box ref={ref} sx={{ position: 'relative' }}>
        <Box
          component={animated.svg}
          width={viewBoxSize}
          height={viewBoxSize}
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          sx={{ verticalAlign: 'top' }}
          style={styles}
        >
          <animated.circle
            cx={viewBoxSize / 2}
            cy={viewBoxSize - circleSize}
            r={circleSize}
            fill="#DDDDDD"
            stroke="none"
          />
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          sx={{
            position: 'absolute',
            width: circleSize * 2,
            height: circleSize * 2,
            top: viewBoxSize - circleSize * 2,
            left: '50%',
            transform: 'translateX(-50%)',
          }}
        >
          <Typography
            variant="h5"
            fontWeight="bold"
            color={grey['700']}
            sx={{ fontSize: { md: 'h5.fontSize', xs: 'h6.fontSize' } }}
          >
            CLICK ME
          </Typography>
        </Box>
      </Box>
      <Typography variant="subtitle1" fontWeight="bold" sx={{ marginTop: 2, textAlign: 'center', color: grey['800'] }}>
        루비콘과 함께할
        <br />
        멘토를 모집하고 있어요!
      </Typography>
    </Box>
  );
};

export default RecruitMentorCircle;
