import React, { useMemo } from 'react';
import { Theme, Typography, Box, useTheme } from '@mui/material';
import FadeUp from 'components/Animated/FadeUp';
import SvgElements from './SvgElements';
import differenceInCalendarDays from 'date-fns/differenceInCalendarDays';
import { SxProps } from '@mui/system';
import sandboxData from 'data/sandbox.json';
import { useSandboxApply } from 'src/hooks/useSandboxApply';
import { SandboxApplyStatus } from 'src/models/sandbox';

const today = new Date();
const endDate = new Date(sandboxData.applyEndDate);
const expiryDiff = differenceInCalendarDays(endDate, today);

const descriptionStyle: SxProps<Theme> = {
  textAlign: 'center',
  marginTop: {
    md: '32px',
    xs: '16px',
  },
  fontSize: {
    md: 18,
    xs: 14,
  },
};

const IntroSection = () => {
  const { spacing } = useTheme<Theme>();
  const { status } = useSandboxApply();
  const dday = useMemo(() => {
    if (expiryDiff === 0) {
      return 'D-Day';
    } else if (expiryDiff < -100) {
      return '';
    } else if (expiryDiff < 0) {
      return '신청 마감';
    } else {
      return `D-${expiryDiff}`;
    }
  }, []);

  return (
    <Box
      sx={{ height: `100vh`, backgroundColor: '#FFFFFF', padding: `0 ${spacing(2)}` }}
      display="flex"
      justifyContent="center"
      alignItems="center"
    >
      <Box display="flex" flexDirection="column" alignItems="center" sx={{ zIndex: 1 }}>
        {dday === '' ? null : (
          <Box sx={{ backgroundColor: '#C5E5DD', padding: '0 14px' }}>
            <FadeUp delay={0}>
              <Typography
                variant="h1"
                sx={{
                  whiteSpace: 'nowrap',
                  fontWeight: 'bold',
                  fontSize: {
                    md: 24,
                    xs: 16,
                  },
                  lineHeight: 1,
                  transform: 'translateY(-8px)',
                }}
              >
                샌드박스 {status === SandboxApplyStatus.사전신청 ? '사전신청' : '신청'} 마감 {dday}
              </Typography>
            </FadeUp>
          </Box>
        )}
        <FadeUp delay={0}>
          <Typography
            variant="h2"
            sx={{
              fontWeight: 'bold',
              fontSize: {
                md: 60,
                xs: 24,
              },
              marginTop: {
                md: '22px',
                xs: 0,
              },
              lineHeight: '81px',
              '& strong': { color: 'primary.main' },
            }}
          >
            LUBYCON <Box component="strong">SANDBOX</Box>
          </Typography>
        </FadeUp>
        <FadeUp delay={200}>
          <Typography sx={descriptionStyle}>
            우리가 선망하는 대부분의 일 잘 하는 회사들은 대부분 애자일, 린 방법론 등을 사용하며 최소 비용으로 제품을
            릴리즈하고
            <br />
            사용자의 행동 데이터를 기반으로 가설을 검증하는 이터레이션을 반복하고 있어요.
          </Typography>
        </FadeUp>
        <FadeUp delay={400}>
          <Typography sx={descriptionStyle}>
            그래서 개발자나 디자이너들도 이제 더 이상 코딩이나 디자인만 잘 하는 것이 아니라,
            <br />
            비즈니스에 대한 관심을 가지고 임팩트를 만들어 낼 수 있는 사람이 인정받는 시대가 된 것이죠.
          </Typography>
        </FadeUp>
        <FadeUp delay={600}>
          <Typography sx={descriptionStyle}>
            이런 지식들은 보통 일 잘하는 회사에 들어가면 자연스럽게 배운다고들 하던데...
            <br />
            그런 회사에 다니지 않고 있는 나는 도대체 이런 것들을 어디서 배워야 하는 걸까요?
          </Typography>
        </FadeUp>
      </Box>
      <Box sx={{ zIndex: 0 }}>
        <SvgElements />
      </Box>
    </Box>
  );
};

export default IntroSection;
