import React from 'react';
import {
  Card,
  CardContent,
  Container,
  Grid,
  Typography,
  Avatar,
  CardHeader,
  CardActionArea,
  Theme,
  Box,
  useTheme,
} from '@mui/material';
import { useMentoringReviews } from 'src/hooks/useMentoringReview';

const ReviewSection = () => {
  const { spacing } = useTheme<Theme>();
  const reviews = useMentoringReviews();

  return (
    <Box component="section" sx={{ padding: `${spacing(15)} 0` }}>
      <Container>
        <Typography
          variant="h4"
          component="h2"
          sx={{
            fontWeight: 'bold',
            textAlign: 'center',
            marginBottom: {
              md: 2,
              xs: 1,
            },
            fontSize: {
              md: 'h2.fontSize',
              xs: 20,
            },
          }}
        >
          멘토링 후기
        </Typography>
        <Typography
          sx={{
            marginBottom: {
              md: 10,
              xs: 4,
            },
            textAlign: 'center',
            fontSize: {
              md: 18,
              xs: 16,
            },
          }}
        >
          루비콘 멘토링 프로젝트에 참여했던 멘티들이 느꼈던 생생한 경험이 궁금하다면
          <br />
          멘티들이 직접 작성한 후기 포스팅을 한번 읽어보세요.
        </Typography>
        <Grid container spacing={2}>
          {reviews.map(({ title, author, url, description }, index) => (
            <Grid item key={index} xs={12} sm={3}>
              <Card onClick={() => window.open(url)}>
                <CardActionArea>
                  <CardHeader
                    avatar={<Avatar alt={author.name} src={author.profileImg} />}
                    title={author.name}
                    subheader={author.role}
                  />
                  <CardContent>
                    <Typography variant="body1" gutterBottom={true} sx={{ fontWeight: 'bold' }}>
                      {title}
                    </Typography>
                    <Typography variant="body2">{description}</Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
};

export default ReviewSection;
